import './img.css';
import { useState } from 'react';

export default function Img({ imageSrc, link, title  }) {
    let [ className, setClassName ] = useState('untoggled');
    let [ greg, setGreg ] = useState(false);

    function hover() {
        setClassName(greg ? 'untoggled' : 'toggled');
        setGreg(!greg);
    }
  return (
    <div onMouseLeave={hover} onMouseEnter={hover} className='contentContainer'>
        <div className={`${className} imageCover`}>
            <b>{title}</b>
            <a rel='noreferrer' target={'_blank'} href={link} className='btn btn-success visitBtn'>Visiter</a>
        </div>
        <img style={{width: "100%"}} src={imageSrc} className={'imageInRS'} alt='' />
    </div>
  )
}
